.popup {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s all;
}

.popup.active {
  opacity: 1;
  pointer-events: all;
}

.popup__content {
  padding: 10px;
  border-radius: 10px;
  background-color: #fff;
  width: 25vw;
  transform: scale(0.5);
  transition: 0.3s all;
}

.popup__content.active {
  transform: scale(1);
}